import handleError from '../utils/handleError'
import { auth, database, firebase } from './Initial'

import { fetch as SectionFetch} from '../../Services/Firebase/Sections'

async function fetch({FarmId, PenId, SectionId, limitStart = 0, limitTo = 9999, DataTime, MemSections}, _rend) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId) {
      console.log("Weight fetch called, getting Pen")
      ref += "/" + PenId
      var res = {[PenId]: {}}
      var state = _rend.getState()
      if (state.firebase.weighings){
        res = {...state.firebase.weighings}
      }
      var te = await database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', async snapshot => {
        res[snapshot.key] = {}
        snapshot.forEach(c => {
          res[snapshot.key][c.key] = c.val()
        })
      }).catch(error => {
        console.log(error)
        return handleError({ message: 'Fecth Weights Error' })
      })
      return res
    } else {
      console.log("Weight fetch called, getting Sections")
      var newWeights = {}
      await SectionFetch({FarmId}).then(async (sections) => {
        var Sections = sections?sections:MemSections
        await Promise.all(Object.entries(Sections).map(async ([section,data]) => {
          if (data && data.pens) {
            await Promise.all(Object.entries(data.pens).map(async ([pen,data]) => {
              if (pen !== "update_time") {
                await fetch({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}, _rend).then(async (weights) => {
                  if (Object.keys(weights[Object.keys(weights)[0]]).length > 0) {
                    newWeights = {...newWeights, ...weights}
                  }
                }).catch(error => {
                  console.log(error)
                })
              }
            }))
          }
        }))
      }).catch(error => {
        console.log(error)
        return handleError({ message: 'Fecth Weights Error' })
      })
      if (Object.keys(newWeights).length > 0) {
        newWeights.update_time = Date.now()
        return newWeights
      }
    }
  }
  return handleError({ message: 'Fecth Weights Error' })
}

async function create({FarmId, PenId, Weight, Quantity, Date}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      ref += "/" + PenId + "/"

      const weightRef = await database().ref(ref).push()
      const weightId = weightRef.key
      var res = await weightRef.set({
        time: Date,
        weight: Weight,
        count: Quantity,
      }).then(async () => {
        var res2 = await fetch({FarmId:FarmId, PenId:PenId, DataTime: "SET"}).then((weights)=>{
          return weights
        })
        return res2
      }).catch(error => {
        console.log("Create Weight Error: ", error);
        return handleError({ message: 'Create Weight Error' })
      })
      return res
    } else {
      //...Weight other than PenId ??
    }
  }
  return handleError({ message: 'Create Weights Error' })
}

export {
  fetch,
  create,
}