import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView,
  Picker,
  CheckBox
} from 'react-native'
import { Input } from 'react-native-elements'
import { LoadingSplash, Brand, Frame, Button, ContentProp, Datetime_Picker } from '../../../Components'

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { sortObject } from '../../../Services/utils/formators'
import { ErrorHandler } from '../../../Store/Firebase/Utils'
import FirebaseFarm from '../../../Store/Firebase/Farm'
import FirebaseSection from '../../../Store/Firebase/Section'
import FirebasePen from '../../../Store/Firebase/Pen'
import FirebaseWeighing from '../../../Store/Firebase/Weighing'

const Weighing = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)
  
  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const activeData = useSelector(state => state.firebase.activeData?state.firebase.activeData:undefined)
  const sectionDatas = useSelector(state => state.firebase.sections)
  
  const [Sections, setSections] = useState({})
  const [Pens, setPens] = useState({})
  const [SelectedSection, setSelectedSection] = useState("")
  const [SelectedPen, setSelectedPen] = useState("")

  var lastWeighingPen = null
  const weighingsDatas = useSelector(state => SelectedPen&&state.firebase.weighings&&state.firebase.weighings[SelectedPen]?state.firebase.weighings[SelectedPen]:undefined)
  const penDatas = useSelector(state => SelectedPen&&state.firebase.pens&&state.firebase.pens[SelectedPen]?state.firebase.pens[SelectedPen]:undefined)

  const [WeightDate, setWeightDate] = useState(new Date())
  const [Quantity, setQuantity] = useState(0)
  const [Weight, setWeight] = useState(0)

  const [SelectedWeight, setSelectedWeight] = useState("")
  const [Edit, setEdit] = useState(false)
  
  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (farmId && (!sectionDatas || typeof sectionDatas === 'undefined')) {
      dispatch(FirebaseSection.action({FarmId: farmId}))
    }
  },[farmId])

  useEffect(() => {
    var _Sections = sectionDatas?sortObject(sectionDatas):{}
    var Section = _Sections?Object.keys(_Sections)[0]:""
    if (activeData && activeData.section && _Sections[activeData.section]){
      Section = activeData.section
    }
    setSections(_Sections)
    setSelectedSection(Section)
  },[sectionDatas, activeData])

  useEffect(() => {
    var _Pens = Sections&&Sections[SelectedSection]?sortObject(Sections[SelectedSection].pens):{}
    var Pen = _Pens? Object.keys(_Pens)[0]:""
    if (activeData && activeData.pen && _Pens[activeData.pen]){
      Pen = activeData.pen
    }
    setPens(_Pens)
    setSelectedPen(Pen)
  },[SelectedSection, Sections])

  useEffect(() => {
    if (SelectedPen && lastWeighingPen !== SelectedPen) {
      lastWeighingPen = SelectedPen
      dispatch(FirebaseWeighing.action({FarmId: farmId, PenId: SelectedPen, SectionId: SelectedSection}))
      dispatch(FirebasePen.action({FarmId: farmId, PenId:SelectedPen}))
    }
  },[SelectedPen])

  useEffect(() => {
    ChangeSelectedWeight()
  }, [penDatas])

  const ChangeSelectedWeight = (_Edit = null) => {
    if (!_Edit) _Edit = Edit
    if (_Edit) {
      if(weighingsDatas && Object.keys(weighingsDatas).length > 0) {
        var Id = Object.keys(weighingsDatas)[0]
        var Weight = weighingsDatas[Id]
        setWeightDate(new Date(Weight.time))
        setWeight(Weight.weight)
        setQuantity(Weight.count)
        setSelectedWeight(Id)
      }else {
        setWeightDate(new Date())
        setWeight(0)
        setQuantity(0)
        setSelectedWeight("")
      }
    } else {
      setWeightDate(new Date())
      setWeight(0)
      setQuantity(0)
      setSelectedWeight("")
    }
  }

  useEffect(() => {
    ChangeSelectedWeight()
  },[weighingsDatas])

  if ((sectionDatas && typeof sectionDatas !== 'undefined') && Sections && Pens) {
    return(
      <SafeAreaView style={[Layout.fill, Layout.fullSize]}>
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            <View style={[Layout.fill, Layout.midWidth, Common.border, {padding:5, minWidth:330}]}>
              <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between', marginBottom:4}]}>
                <View style={[Layout.column, Layout.halvWidth]}>
                  <Picker
                    mode="dropdown"
                    selectedValue={SelectedSection}
                    style={ [Layout.midWidth, { height: 50, }]}
                    onValueChange={(itemValue, itemIndex) => {
                      if (Sections[itemValue]) {
                        setSelectedSection(itemValue)
                      }
                    }}
                  >
                    {Object.keys(Sections).map((Section, i) => {
                        return (<Picker.Item label={Section} value={Section} key={i}/>)
                    })}
                  </Picker>

                  <Picker
                    mode="dropdown"
                    selectedValue={SelectedPen}
                    style={ [Layout.midWidth, { height: 50 }]}
                    onValueChange={(itemValue, itemIndex) => {
                      if (Pens[itemValue]) {
                        setSelectedPen(itemValue)
                      }
                    }}
                  >
                    {Object.keys(Pens).map((Pen, i) => {
                      return (<Picker.Item label={Pen} value={Pen} key={i}/>)
                    })}
                  </Picker>
                </View>
                <View style={[Layout.column, Layout.halvWidth, {alignItems:'flex-end'}]}>
                  <View style={[Layout.row, Layout.fullWidth, {borderWidth:0, height:50, alignItems:'center', justifyContent:'flex-end'}]}>
                    <Text style={[{marginLeft: 5}]}>Edit Weights: </Text>
                    <CheckBox style={[{marginTop:2, marginHorizontal:5}]}
                      value={Edit}
                      onValueChange={() => {
                        var _Edit = !Edit
                        setEdit(_Edit)
                        ChangeSelectedWeight(_Edit)
                      }}
                    />
                  </View>
                  {Edit&&weighingsDatas&&(
                    <View style={[Layout.fullWidth, {justifyContent:'flex-end'}]}>
                      <Picker
                        mode="dropdown"
                        selectedValue={SelectedWeight}
                        style={[ Layout.fullWidth, { height: 50, alignSelf:'flex-end' }]}
                        onValueChange={(itemValue, itemIndex) => {
                          var Weight = weighingsDatas[itemValue]
                          setWeightDate(new Date(Weight.time))
                          setWeight(Weight.weight)
                          setQuantity(Weight.count)
                          setSelectedWeight(itemValue)
                        }}
                      >
                        {Object.entries(weighingsDatas).map(([Id,weight]) => {
                          var d = new Date(weight.time)
                          var time = d.getFullYear() + "/" + (d.getMonth() +1) + "/" + d.getDate()
                          var name = time + " - " + weight.count + ":" + weight.weight
                          return (<Picker.Item label={name} value={Id} key={Id} />)
                        })}
                      </Picker>
                    </View>
                  )}
                </View>
              </View>

              {!Edit?(
                <View style={[isApp()?{}:Layout.halvWidth, {width:315, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1}]}>
                  <View style={[Layout.fullWidth]}>
                    <View style={[Layout.fullWidth, {alignItems:"flex-start", height:22}]}>
                      <Text style={[Layout.fullWidth, {marginHorizontal:5, marginVertical:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Weight date:</Text>
                    </View>
                    <View style={[Layout.fullWidth, isApp()?{marginBottom:10}:{}, {alignItems:'flex-start', height:28}]}>
                      <Datetime_Picker Selected={WeightDate}
                        onValueChange={(date)=>{
                          setWeightDate(date)
                        }}
                      />
                    </View>
                  </View>
                </View>
              ):(
                <View style={[Layout.halvWidth, {alignSelf:'flex-end'}]}>
                  <View style={[Layout.midWidth, {alignSelf:'center', marginTop:8}]}>
                    <Button Texts={["Delete weighing"]}
                      Actions={[() => {
                        //var Weight = this.state.Weights[this.state.Weight.id]
                        if (!SelectedSection || SelectedSection === ""){
                          //this.props.setViewError("A section is needed")
                        } else if (!SelectedPen || SelectedPen === ""){
                          //this.props.setViewError("A pen is needed")
                        } else if (!Weight || !Weight.id) {
                          //this.props.setViewError("A selected Weight is needed")
                        }
                        else {
                          //this.props.setViewLoading(true)
                          //this.props.setViewError(null)
                          //this.props.deleteWeight(this.state.Pen, this.state.Weight.id)
                          /*this.setState({
                            Edit: false,
                          }, () => {
                            this.ChangeSec_Pen(this.state.Section, this.state.Pen)
                          })*/
                        }
                      }]}
                    />
                  </View>
                </View>
              )}

              <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between'}]}>
                <View style={[Layout.fillCol, Layout.halvWidth]}>
                  <Input
                    label="Animal quantity"
                    style={[Common.textInput, {width:100}]}
                    containerStyle={{maxWidth:315}}
                    onChangeText={text => {
                      if (!isNaN(text) && !text.endsWith('.')){
                        setQuantity(text)
                      }
                    }}
                    value={Quantity}
                  />
                </View>
                <View style={[Layout.column, Layout.halvWidth, {alignItems:'flex-end'}]}>
                </View>
              </View>
              <View style={[Layout.fullWidth, Layout.row, {justifyContent:'space-between'}]}>
                <View style={[Layout.fillCol, Layout.halvWidth]}>
                  <Input
                    label="Total Weight"
                    style={[Common.textInput, {width:100}]}
                    containerStyle={{maxWidth:315}}
                    onChangeText={text => {
                      if (!isNaN(text)){
                        setWeight(text)
                      }
                    }}
                    value={Weight}
                  />
                </View>
                <View style={[Layout.column, Layout.halvWidth, {alignItems:'flex-end'}]}>
                  <View style={[Layout.midWidth, {alignSelf:'center', marginTop:8}]}>
                    <Button Texts={["Corridor Scale"]}
                      Actions={[() => {
                        /*this.setState({
                          waitForScale:true
                        })
                        this.props.getScaleItem(this.state.CorridorScale.sysid, this.state.Pen)*/
                      }]}
                    />
                  </View>
                </View>
              </View>
            </View>
          </View>
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Weighing
